@charset "utf-8";
*{margin: 0; padding: 0;}
ul, li, ol {list-style: none;}
a{text-decoration: none; color:#fff;}

/* 헤더서식 */
.header{
  width: 90%; height: 100px; color: #fff; position: fixed; z-index: 1000000; margin: 0 auto; left: 9%;
}
.header h1{width: 200px;}
.header h1 img{width: 200px;}

.login_box{width: 100px; position: absolute; top: 30px; right:13%;}
.login_box a{padding: 7px 10px; border-radius: 4px; text-align: center; background: #e50914; font-size: 14px;}

/* 푸터서식 */
footer{width: 100%; height: 50px; background: #000;} 
address{color: #fff; font-style: normal; text-align: center; font-size: 0.75rem; padding-top: 15px;}
