@charset "utf-8";

@keyframes fadeIn{
  from{opacity:0;}
  to{opacity: 1;}
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* modal 서식 */
.modal_overlay{
  position: fixed; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.75); display: flex; align-items: center; justify-content: center; z-index: 9999; animation: fadeIn 0.3s ease;
}

.modal_content{
  background: #111; color:#fff; border-radius: 10px; width: 90%; max-width: 800px; padding: 20px; position: relative; animation: slideUp 0.4s ease; box-shadow: 0 0 30px rgba(0,0,0,0.7); height: 70%;
}

.modal_close{
  position: absolute; top: 15px; right: 20px; background: transparent; border: none; font-size: 2rem; color: #fff; cursor: pointer;
}

.modal_img{width: 100%; max-width: 800px; object-fit: cover; border-radius: 10px;}

.modal_info{margin-top: 20px;}

.modal_title{font-size: 2rem; margin-bottom: 30px;}
.modal_overview{font-size: 1rem; line-height: 1.5; color: #ccc; margin-top: 1rem;}
.modal_rating{margin-top: 10px; font-weight: bold; color: #ffcc00;}

/* 뱃지 서식 */
.modal_badges{
  display: flex; flex-wrap: wrap; gap: 8px; margin-bottom: 1rem;
}
.badge{
  background: #e50914; color: #fff; padding:4px 10px; border-radius: 5px; margin-top: 10px; font-size: 0.75rem; font-weight: 600; display: inline-block;
}
.badge.gray{background: #444;}
.badge.red{background: #b81d24;}

@media (max-width:768px){
  .modal_content{
  height: 95%;
  }
  .modal_overview{font-size: 0.9rem;}
}