@charset "utf-8";

/* qna 서식 */

.qna{
  width: 100%; height: 100%; margin: 50px auto;
}
.qna h2{ color: #fff; padding:20px; font-size: 1.75rem; text-align: center;}

.qna_item{width: 100%; margin: 0 auto; align-items: center;}
.qna_btn{display: block; width: 50%; text-align: left; font-size: 1.5rem; margin: 5px auto; border:none; background: #2d2d2d; color:#fff; padding: 20px 10px; border-radius: 5px; transition: 0.3s; cursor: pointer;}
.qna_btn:hover{background: #4b4b4b;}
.angle{float: right; transform: translateY(5px); padding-right: 10px;}
.qan_info_wrapper{overflow: hidden; transition: max-height 0.4s ease; max-height: 0;}
.qna_info{transition: 0.3s; background: #2d2d2d; width: 49%; color: #fff; padding: 20px 10px; border-radius: 0px 0px 5px 5px; line-height: 30px; font-size: 1.3rem; margin: 0 auto 20px auto; opacity: 0; transition: opacity 0.4s ease; border-bottom: 2px solid #e50914;}
.qna_info_wrapper .active{max-height: 300px; opacity: 1;}
.qna_info span{white-space: pre-line;}
.qna_info_wrapper {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease;
}


.qna_info_wrapper.active {
  max-height: 500px;
}

.qna_info_wrapper.active .qna_info {
  opacity: 1;
}

/* 테블릿 */
@media (max-width:1024px) and (min-width:768px){
.qna_btn{width: 90%;}
.qna_info{width: 88%;}
}

/* 모바일 */
@media (max-width:767px){
.qna_btn{width: 90%; font-size: 0.75rem; padding: 10px 5px;}
.qna_info{width: 85%; font-size: 0.75rem; line-height: 20px;}
}
