@charset "utf-8";

/* row 서식 */
body{background: #111;}
.row{margin: 0 auto; color:#fff; width: 1200px;}
.row_container{position: relative;}
.row_posters{
  display: flex; overflow-y: hidden; overflow-x: auto; padding: 20px 0; scrollbar-width: none; -ms-overflow-style: none;
  white-space: nowrap; scroll-behavior: smooth;
}

.row_poster{
  width: 200px; object-fit: contain; margin-right: 10px; transition: transform 0.3s; border-radius: 5px; cursor:pointer;
}
.row_poster:hover{transform: scale(1.08);}

.row_posters::-webkit-scrollbar{display: none;}

/* 좌우 버튼 서식 */
.btn{border: none; color: #fff; position: absolute; top: 50%;  transform: translateY(-50%); z-index: 10; background: rgba(0,0,0,0.5); font-size: 2rem; cursor: pointer; padding: 10px; border-radius: 10px; transform: background-color 0.2s;}
.btn:hover{background-color:rgba(255,255,255,0.2)}
.left_btn{left: 0;}
.right_btn{right: 0;}