@charset "utf-8";

:root{
  --main_color:#e50914;
}
/* 로그인 폼 */
.login{padding-top:100px; width:100%; height: 100vh; background-image: url(../../public/images/form_bg.jpg); overflow: hidden; background-repeat: no-repeat; background-position: center; background-size: cover;
position: relative;}
.login::after{content:''; background: rgba(0,0,0,0.5); position: absolute; top: 0; left: 0; right: 0; bottom: 0; pointer-events: none;}
form{width: 400px; color: #fff; background: rgba(0,0,0,0.7); padding: 50px; border-radius: 10px; position: absolute; top: 10%; left: 50%; transform: translateX(-50%); z-index: 2;}
form h3{text-align: left; font-size: 30px; color: #fff;}

label{font-weight: bold; font-size: 1rem; color: #b3b3b3;}
.login_input{ width: 350px; padding: 30px 10px 10px 10px; margin-top: 20px; margin-left: 10px; background: rgba(43, 43, 43, 0.7); border: none; border-radius: 5px; color: #fff; font-size: 1rem;}

/* id, password 클릭시 애니메이션 */
.login_id, .login_password{position: relative;}
.login_id label, .login_password label{position: absolute; top: 45%; left:7%; font-size: 1rem; transition: 0.3s; pointer-events: none;}
.login_id input:focus + label,
.login_id input:valid + label,
.login_password input:focus + label,
.login_password input:valid + label
{top: 30%; left: 5%; font-size: 0.75rem; color:#ccc;}

/* 로그인버튼 */
.login_btn{width: 100%px; margin: 30px 0 10px 0;}
.login_btn button{width: 373px; margin-left: 2%; padding: 10px 0px; background: var(--main_color); border: none; border-radius: 5px; color: #fff; font-weight: bold; cursor: pointer;}
.and{text-align: center; color: #ccc;}
.login_code{width: 100%; margin: 10px 0 10px 0;}
.login_code button{width: 373px; margin-left: 2%; padding: 10px 0px; background: rgba(199, 199, 199, 0.897); border: none; border-radius: 5px; color: #fff; font-weight: bold; cursor: pointer;}

.password_find{width: 400px; text-align: center;}
.password_find a{text-decoration: underline;}

/* 로그인 정보 저장 체크박스 */
.login_save{width: 400px; margin: 15px 0px;}
.login_save #save{width: 15px; height: 15px; margin-right: 10px;}
.login_save #save:checked{color: var(--main_color);}
.login_save label{font-size: 1rem; transform: translateY(30px);}

/* 회원가입 */
.joing{margin: 15px 0px;}
.joing span{color: #b3b3b3; padding-right: 5px;}

/* 로봇체크 안내문 */
.robot_check{font-size: 0.8rem; width: 300px; padding-bottom: 15px;}

/* 자세히보기 버튼 */
.detail{border: none; background: none; color:rgb(50, 50, 248); text-decoration: underline; cursor: pointer;}