@charset "utf-8";

/* banner 서식 */

.banner{
  position: relative; color: #fff; height: 720px;  background-size: cover; background-position: center center; object-fit:contain;
}

.banner_content{
  margin-left: 30px; padding-top: 140px; height: 190px;
}
.banner_title{
  font-size: 3rem; font-weight: bold; padding-bottom: 0.3rem;
}

/* 배너버튼 */
.banner_btns{
  margin-top:1rem;
}
.banner_btn{border: none; background: rgba(0,0,0,0.5); transition: background 0.3s; color: #fff; cursor: pointer; font-weight: bold; border-radius: 5px; padding: 0.5rem 2rem; margin-right: 5px; border: 1px solid rgba(0,0,0,.5);}
.banner_btn:hover{background: rgba(255,255,255,0.5); color: #222; font-weight: bold; border:1px solid #ccc}

.banner_description{
  width: 45rem; max-width: 90vw; line-height: 1.3; padding-top: 1rem; font-size: 1rem; max-height: 80px; overflow: hidden; text-overflow: ellipsis;
}
.banner_fade_bottom{
  height: 7.4rem; background-image: linear-gradient(
    180deg,
    transparent, rgba(37,37,37,0.61), #111
    );
    position: absolute; bottom: 0; width: 100%;
}